<template>
    <v-container>
        <v-row>
            <v-col>
                <h1>Chc nng dnh cho Bc s a khoa</h1>
            </v-col>
        </v-row>
        <v-row wrap row>
            <v-col cols="12" sm="12" md="12" lg="6" xl="6">
                <v-card class="elevation-4" height="100%">
                    <v-data-table class="elevation-0" no-data-text="Khng c yu cu t vn no" loading-text="ang ly d liu" :loading="loadingInquiries" :headers="inquiryHeadersMain" :items="inquiries" hide-default-footer>
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title><h3>Yu cu t vn</h3></v-toolbar-title>
                                <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                                ></v-divider>
                                <v-spacer></v-spacer>
                                <v-select :items="inquiryAssignValue" v-model="inquiryAssign" @input="inquiryPage = 1, getInquiries(inquiryPage, inquiryPageSize, inquiryAssign)"></v-select>
                            </v-toolbar>
                        </template>
                        <template v-slot:item.patient="{ item }">
                            {{ item.patient.name }}
                        </template>
                        <template v-slot:item.type="{ item }">
                            {{ returnInquiryType(item.type) }}
                        </template>
                        <template v-slot:item.more="{item}">
                            <v-menu offset-y>
                                <template v-slot:activator="{ on }">
                                    <v-icon v-on="on">more_vert</v-icon>
                                </template>
                                <v-list>
                                    <v-list-item @click="getDetailInquiry(item.id)">
                                        <v-list-item-title>Xem chi tit</v-list-item-title>
                                    </v-list-item>
                                    <v-list-item @click="openAssignToDoctorMainDialog(item)"><v-list-item-title>Yu cu hi chn chuyn khoa</v-list-item-title></v-list-item>
                                </v-list>
                            </v-menu>
                        </template>
                        <template v-slot:footer>
                            <br>
                            <div class="text-center">
                                <v-pagination
                                    :total-visible="7"
                                    v-model="inquiryPage"
                                    :length="inquiryPages"
                                    @input="getInquiries(inquiryPage, inquiryPageSize, inquiryAssign)"
                                ></v-pagination>
                            </div>
                            <br>
                        </template>
                    </v-data-table>
                    
                </v-card>
                <v-dialog scrollable v-model="assignToDoctorDialogMain" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">Yu cu hi chn chuyn khoa</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col  sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedInquiryMain"
                                            :headers="inquiryHeaders"
                                            :items="selectInquiryMain"
                                            single-expand
                                            show-expand
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                            <template v-slot:expanded-item="{ item }">
                                                <td :colspan="inquiryHeaders.length"><b>Yu cu t vn: {{ item.content }}</b> </td>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                    <v-col sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedDoctor"
                                            :headers="doctorHeaders"
                                            :items="selectableDoctor"
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>{{selectDoctorTitle}}</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue" text :disabled="selectedInquiryMain.length == 0 || selectedDoctor == 0" @click="assignToPrac(selectedInquiryMain[0].patient.id, selectedInquiryMain, selectedDoctor), assignToDoctorDialogMain = false, selectedInquiryMain = [], selectedDoctor = [], selectInquiryMain = []">GN</v-btn>
                            <v-btn color="red" text @click="assignToDoctorDialogMain = false, selectedInquiryMain = [], selectedDoctor = [], selectInquiryMain = []">NG</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-col>
            <v-col cols="12" md="12" sm="12" lg="6" xl="6">
                <v-card class="elevation-4" height="100%">
                    <v-data-table class="elevation-0" no-data-text="Khng c kt qu ph hp" loading-text="ang ly d liu" :loading="loadingPatient" v-show="showPatient" :headers="patientHeaders" :items="allPatients" hide-default-footer>
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title><h3>Bnh nhn qun l</h3></v-toolbar-title>
                                <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                                ></v-divider>
                                <v-spacer></v-spacer>
                                <v-menu
                                    v-model="patientSearchMenu"
                                    :close-on-content-click="false"
                                    :nudge-width="200"
                                    offset-x
                                    >
                                    <template v-slot:activator="{ on }">
                                        <v-btn
                                        color="primary"
                                        dark
                                        text
                                        v-on="on"
                                        >
                                        <v-icon>search</v-icon>
                                        Tm kim
                                        </v-btn>
                                    </template>

                                    <v-card>
                                        <v-card-title>
                                            <h4>Tm kim bnh nhn</h4>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-text-field clearable v-model="patientSearch.name" label="Tn"></v-text-field>
                                            <v-select clearable v-model="patientSearch.gender" :items="genderArr" label="Gii tnh"></v-select>
                                            <v-text-field clearable type="number" v-model="patientSearch.age" label="Tui"></v-text-field> 
                                            <span>
                                                <v-checkbox @change="setPatientStatusBit(6, patientStatusBitAllValue[6])" v-model="patientStatusBitAllValue[6]" label="Tt c bnh nhn qun l"></v-checkbox>
                                                <!-- <v-checkbox @change="setPatientStatusBit(5, patientStatusBitAllValue[5])" v-model="patientStatusBitAllValue[5]" label="Bnh nhn ch c bc s a khoa"></v-checkbox> -->
                                                <v-checkbox @change="setPatientStatusBit(2, patientStatusBitAllValue[2])" v-model="patientStatusBitAllValue[2]" label="Bnh nhn c bc s chuyn khoa"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(3, patientStatusBitAllValue[3])" v-model="patientStatusBitAllValue[3]" label="Bnh nhn c bc s dinh dng"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(4, patientStatusBitAllValue[4])" v-model="patientStatusBitAllValue[4]" label="Bnh nhn c lch gi ch s sc khe"></v-checkbox>
                                            </span>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary" text @click="patientSearchMenu = false, patientPage = 1, getAllPatients(patientPage, patientPageSize, patientSearch)">Tm kim</v-btn>
                                            <v-btn text color="red" @click="patientSearchMenu = false">ng</v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-menu>
                            </v-toolbar>
                        </template>
                        <template v-slot:item.name="{item}">
                            <a @click="getPatientDetail(item.id, true)">{{item.name}}</a>
                        </template>
                        <template v-slot:item.more="{item}">
                            <v-menu offset-y>
                                <template v-slot:activator="{ on }">
                                    <v-icon v-on="on">more_vert</v-icon>
                                </template>
                                <v-list>
                                    <v-list-item @click="getPatientDetail(item.id, true)">
                                        <v-list-item-title>Xem chi tit</v-list-item-title>
                                    </v-list-item>
                                    <v-list-item @click="getPatientDetail(item.id, false)"><v-list-item-title>Gn bnh nhn</v-list-item-title></v-list-item>
                                    <v-list-item @click="newSchedule.patientId = item.id, newScheduleDialog = true">
                                        <v-list-item-title>To lch gi ch s sc khe</v-list-item-title>
                                    </v-list-item>
                                    <v-list-item @click="removePatientObj.id = item.id, removePatientObj.dialog = true"><v-list-item-title>Xa bnh nhn khi quyn qun l</v-list-item-title></v-list-item>
                                </v-list>
                            </v-menu>
                        </template>
                        <template v-slot:footer>
                            <br>
                            <div class="text-center">
                                <v-pagination
                                    :total-visible="7"
                                    v-model="patientPage"
                                    :length="patientPages"
                                    @input="getAllPatients(patientPage, patientPageSize, patientSearch)"
                                ></v-pagination>
                            </div>
                            <br>
                        </template>
                    </v-data-table>
                    
                </v-card>
                <v-dialog scrollable v-model="patientDetailDialog" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            Chi tit bnh nhn
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12"  sm="12" md="6" lg="6" xl="3">
                                        <v-text-field readonly label="ID" v-model="patientDetail.id"></v-text-field>
                                    </v-col>
                                    <v-col cols="12"  sm="12" md="6" lg="6" xl="3">
                                        <v-text-field readonly label="Tn" v-model="patientDetail.name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12"  sm="12" md="6" lg="6" xl="3">
                                        <v-text-field readonly label="Ngy sinh" v-model="patientDetail.dateOfBirth"></v-text-field>
                                    </v-col>
                                    <v-col cols="12"  sm="12" md="6" lg="6" xl="3">
                                        <v-text-field readonly label="Gii tnh" v-model="patientDetail.gender"></v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field readonly label="Email" v-model="patientDetail.email"></v-text-field>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-text-field readonly label="S in thoi" v-model="patientDetail.phoneNumber"></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col  sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            :headers="inquiryHeaders"
                                            :items="patientDetail.inquiries"
                                            single-expand
                                            show-expand
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <template v-slot:expanded-item="{ item }">
                                                <td :colspan="inquiryHeaders.length"><b>Yu cu t vn: {{ item.content }}</b> </td>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                    
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red" text @click="patientDetailDialog = false">Close</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog scrollable v-model="assignToDoctorDialog" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">Gn quyn qun l bnh nhn cho Bc s chuyn khoa/dinh dng</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col  sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedInquiry"
                                            :headers="inquiryHeaders"
                                            :items="patientDetail.inquiries"
                                            single-expand
                                            show-expand
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                            <template v-slot:expanded-item="{ item }">
                                                <td :colspan="inquiryHeaders.length"><b>Yu cu t vn: {{ item.content }}</b> </td>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                    <v-col  sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedDoctor"
                                            :headers="doctorHeaders"
                                            :items="selectableDoctor"
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>{{selectDoctorTitle}}</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue" text :disabled="selectedInquiry.length == 0 || selectedDoctor == 0" @click="assignToPrac(patientDetail.id, selectedInquiry, selectedDoctor), assignToDoctorDialog = false, selectedInquiry = [], selectedDoctor = []">GN</v-btn>
                            <v-btn color="red" text @click="assignToDoctorDialog = false, selectedInquiry = [], selectedDoctor = []">NG</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-col>
            <v-dialog
                persistent
                v-model="removePatientObj.dialog"
                width="400"
                >
                <v-card>
                    <v-card-title
                        class="headline red"
                        primary-title
                        dark
                        >
                        <span style="color: white">Xc nhn xa</span>
                    </v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <br>
                        Bn c chc chn mun xa bnh nhn ny khi quyn qun l ca mnh?
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="red"
                        text
                        @click="removePatientObj.dialog = false, removePatient(removePatientObj.id)"
                        >
                        XA
                    </v-btn>
                    <v-btn
                        color="primary"
                        text
                        @click="removePatientObj.dialog = false, removePatientObj.id = 0"
                        >
                        NG
                    </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-row>
        <v-row>
            <v-col cols="12" sm="12" md="12" lg="6" xl="6">
                <v-card class="elevation-4" height="100%">
                    <v-data-table class="elevation-0" :headers="healthIndexesSchedules.headers" :items="healthIndexesSchedules.data" hide-default-footer no-data-text="Hin ti cha c lch gi no" loading-text="ang ly d liu..." :loading="healthIndexesSchedules.loading">
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title><h3>Lch gi ch s sc khe</h3></v-toolbar-title>
                                <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                                ></v-divider>
                                <!-- dialog to lch -->
                                <v-dialog v-model="newScheduleDialog" persistent max-width="700px">
                                    <v-card>
                                        <v-card-title
                                            class="headline primary"
                                            primary-title
                                            >
                                            <span style="color: white">To lch gi ch s sc khe</span>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-container>
                                                <v-row>
                                                    <v-col cols="12" sm="6" md="6" lg="4" xl="4">
                                                        <h3>Thi gian nhc nh</h3>
                                                    </v-col>
                                                    <v-col cols="12" sm="3" md="3" lg="4" xl="4">
                                                        <v-menu
                                                            ref="startDateMenu"
                                                            v-model="newSchedule.startDateMenu"
                                                            :close-on-content-click="false"
                                                            :return-value.sync="newSchedule.startDateMenu"
                                                            transition="scale-transition"
                                                            offset-y
                                                            min-width="290px"
                                                            >
                                                            <template v-slot:activator="{ on }">
                                                                <v-text-field
                                                                    v-model="newSchedule.startDate"
                                                                    label="Ngy bt u"
                                                                    readonly
                                                                    prepend-icon="event"
                                                                    v-on="on"
                                                                ></v-text-field>
                                                            </template>
                                                            <v-date-picker v-model="newSchedule.startDate" @input="newSchedule.startDateMenu = false" scrollable></v-date-picker>
                                                        </v-menu>
                                                    </v-col>
                                                    <v-col cols="12" sm="3" md="3" lg="4" xl="4">
                                                        <v-menu
                                                            ref="startTimeMenu"
                                                            v-model="newSchedule.startTimeMenu"
                                                            :close-on-content-click="false"
                                                            :return-value.sync="newSchedule.startTimeMenu"
                                                            transition="scale-transition"
                                                            offset-y
                                                            min-width="290px"
                                                            >
                                                            <template v-slot:activator="{ on }">
                                                                <v-text-field
                                                                    v-model="newSchedule.startTime"
                                                                    label="Gi bt u"
                                                                    readonly
                                                                    prepend-icon="event"
                                                                    v-on="on"
                                                                ></v-text-field>
                                                            </template>
                                                            <v-time-picker v-model="newSchedule.startTime" scrollable @click:minute="newSchedule.startTimeMenu = false"></v-time-picker>
                                                        </v-menu>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col sm="4" md="4" lg="2" xl="2">
                                                        <h3>Chu k nhc nh</h3>
                                                    </v-col>
                                                    <v-col>
                                                        <v-text-field v-model="newSchedule.month" label="Thng" type="number"></v-text-field>
                                                    </v-col>
                                                    <v-col>
                                                        <v-text-field v-model="newSchedule.week" label="Tun" type="number"></v-text-field>
                                                    </v-col>
                                                    <v-col>
                                                        <v-text-field v-model="newSchedule.day" label="Ngy" type="number"></v-text-field>
                                                    </v-col>
                                                    <v-col>
                                                        <v-text-field v-model="newSchedule.hour" label="Gi" type="number"></v-text-field>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col cols="12" sm="12" md="12" lg="12" xl="12">
                                                        <v-data-table 
                                                            class="elevation-4" 
                                                            :headers="healthIndexesHeaders" 
                                                            :items="healthIndexes" 
                                                            show-select
                                                            single-select
                                                            v-model="newSchedule.indexObj"
                                                            hide-default-footer 
                                                            no-data-text="Hin ti cha c chuyn mn no" 
                                                            loading-text="ang ly d liu..." 
                                                            :loading="loadingHealthIndexes"
                                                            show-expand
                                                            single-expand
                                                            >
                                                            <template v-slot:top>
                                                                <v-toolbar flat>
                                                                    <v-toolbar-title><h3>Chn ch s sc khe</h3></v-toolbar-title>
                                                                    <v-divider
                                                                        class="mx-4"
                                                                        inset
                                                                        vertical
                                                                    ></v-divider>
                                                                </v-toolbar>
                                                            </template>
                                                            <template v-slot:expanded-item="{ item }">
                                                                <td :colspan="healthIndexesHeaders.length">
                                                                    <br>
                                                                    <h4>Danh sch cc ch s cn gi</h4>
                                                                    <div v-for="(field) in item.fields" :key="field.id" style="padding-left: 20px;">
                                                                        - {{field.name}} 
                                                                    </div>
                                                                    <br>
                                                                </td>
                                                            </template>
                                                        </v-data-table>
                                                        <br>
                                                        <div class="text-center">
                                                            <v-pagination
                                                                :total-visible="7"
                                                                v-model="healthIndexesPage"
                                                                :length="healthIndexesPages"
                                                                @input="newSchedule.indexObj = [], getHealthIndexes(healthIndexesPage, healthIndexesPageSize)"
                                                            ></v-pagination>
                                                        </div>
                                                    </v-col>
                                                </v-row>
                                            </v-container>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="blue darken-1" text :disabled="(!checkTimeScheduleAvailable) || (newSchedule.indexObj.length == 0)" @click="newScheduleDialog = false, createSchedule(newSchedule)">TO</v-btn>
                                            <v-btn color="red" text @click="newScheduleDialog = false">NG</v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>
                                <v-spacer></v-spacer>
                                <v-menu
                                    v-model="healthIndexesSchedules.searchMenu"
                                    :close-on-content-click="false"
                                    :nudge-width="200"
                                    offset-x
                                    >
                                    <template v-slot:activator="{ on }">
                                        <v-btn
                                        color="primary"
                                        dark
                                        text
                                        v-on="on"
                                        >
                                        <v-icon>search</v-icon>
                                        Tm kim
                                        </v-btn>
                                    </template>

                                    <v-card>
                                        <v-card-title>
                                            <h4>Tm kim lch gi</h4>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-text-field clearable v-model="healthIndexesSchedules.search.id" type="number" label="ID bnh nhn"></v-text-field>
                                            <v-text-field clearable v-model="healthIndexesSchedules.search.name" label="Tn bnh nhn"></v-text-field>
                                            <v-select v-model="healthIndexesSchedules.search.status" label="Trng thi lch gi" :items="healthIndexesSchedules.allStatus"></v-select>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary" text @click="healthIndexesSchedules.searchMenu =  false, healthIndexesSchedules.page = 1, getHealthIndexesSchedule(healthIndexesSchedules.page, healthIndexesSchedules.pageSize, healthIndexesSchedules.search)">Tm kim</v-btn>
                                            <v-btn text color="red" @click="healthIndexesSchedules.searchMenu = false">ng</v-btn>
                                        </v-card-actions>
                                    </v-card>
