    methods: {
        setPatientStatusBit(index, value){
            // console.log(index)
            // console.log(value)
            if(value == true){
                switch(index){
                    //has spec or diet
                    case 2:
                    case 3: {
                        this.patientStatusBitAllValue[5] = 0;
                        this.patientStatusBitAllValue[6] = 0;
                        break;
                    }
                    case 4: {
                        this.patientStatusBitAllValue[6] = 0;
                        break;
                    }
                    // //only has prac
                    // case 5: {
                    //     this.patientStatusBitAllValue[2] = 0;
                    //     this.patientStatusBitAllValue[3] = 0;
                    //     this.patientStatusBitAllValue[6] = 0;
                    //     break;
                    // }
                    //all patient
                    case 6: {
                        this.patientStatusBitAllValue[2] = 0;
                        this.patientStatusBitAllValue[3] = 0;
                        this.patientStatusBitAllValue[4] = 0;
                        this.patientStatusBitAllValue[5] = 0;
                        break;
                    }
                    default: {
                        break;
                    }
                }
            }
            // console.log(this.returnValueFromStatusBit())
        },
        returnValueFromStatusBit(){
            if(this.patientStatusBitAllValue[6] == true || !this.patientStatusBitAllValue.includes(true)){
                return -1;
            }
            // else if(this.patientStatusBitAllValue[5] == true){
            //     return (this.patientStatusBitAllValue[4] == true) ? 18 : 2
            // }
            else {
                let bitArr = []
                for(let i = 0; i < 5 ; i++){
                    bitArr[i] = (this.patientStatusBitAllValue[i] == true) ? 1 : 0
                }
                let statusValue = bitArr.reverse().join("")
                // console.log(statusValue)
                //has been reverse
                // console.log(bitArr)
                return convertNumber.convertToDecimal(statusValue) + 2;
            }
        },
        returnRole(role){
            let result = '';
            switch(role){
                case 'PATIENT':
                    result = 'Bnh nhn';
                    break;
                case 'PRACTITIONER':
                    result = 'Bc s a khoa';
                    break;
                case 'SPECIALIST':
                    result = 'Bc s chuyn khoa';
                    break;
                case 'DIETITIAN':
                    result = 'Bc s dinh dng';
                    break;
                case 'COORDINATOR':
                    result = 'IU PHI VIN';
                    break;
                default:
                    result = '';
                    break;
            }
            return result
        },
        scrollBottom() {
			window.scrollTo(0,document.body.scrollHeight)
		},
        returnTimeFromTimeArray(arr){
            try {
                let i = 0;
                let dayArr = []
                let timeArr = []
                while(i < arr.length){
                    if(i < 3){
                        dayArr.push(arr[i])
                    }
                    else {
                        timeArr.push(arr[i])
                    }
                    i++
                }
                let timeString = `${dayArr.join('-')} ${timeArr.join(':')}`
                return moment(timeString).format('HH:mm:ss DD/MM/YYYY')
            }
            catch(error){
                console.log(error)
                return "_"
            }
        },
        clearSpecAndDietCheck(){
            if(this.patientAllStatus[0].value == true){
