<template>
    <v-container>
        <v-row>
            <v-col>
                <h1>Thng tin ngi dng</h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col xl="12">
                <v-card v-if="userInfo != null">
                    <v-card-title>
                    </v-card-title>
                    <v-card-text>
                        <v-form v-model="valid">
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="12" md="8" lg="3" xl="3">
                                        <v-avatar v-if="userInfo.avatar" width="300" height="300">
                                            <img
                                                :src="userInfo.avatar"
                                                >
                                        </v-avatar>
                                        <br>
                                        <v-file-input accept="image/*" v-model="file" label="Cp nht nh i din ti y" append-icon="edit"></v-file-input>
                                        <v-btn @click="uploadAvatar()" v-if="file" color="primary" rounded>Cp nht nh i din</v-btn>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="12" lg="8" xl="8">
                                        <v-row wrap>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-text-field readonly :rules="noEmptyRules" v-model="userInfo.name" label="Tn ngi dng"></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-text-field readonly :value="returnRole(userInfo.role)" label="Chc v"></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-text-field
                                                    v-model="userInfo.dateOfBirth"
                                                    label="Ngy sinh"
                                                    readonly
                                                    >
                                                </v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                Gii tnh
                                                <v-radio-group v-model="userInfo.gender" row readonly>
                                                    <v-radio label="Nam" value="0"></v-radio>
                                                    <v-radio label="N" value="1"></v-radio>
                                                    <v-radio label="Khc" value="2"></v-radio>
                                                </v-radio-group>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-text-field v-model="userInfo.email" :rules="emailRules" label="Email" append-icon="edit"></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-text-field v-model="userInfo.phoneNumber" :rules="phoneRules" label="S in thoi" append-icon="edit"></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row v-if="userInfo.role != 'COORDINATOR' && userInfo.role != 'PATIENT'">
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-textarea v-model="userInfo.description" rows="4" label="Ghi ch" append-icon="edit"></v-textarea>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-btn @click="updateInfo(userInfo.email, userInfo.phoneNumber, userInfo.description)" :disabled="!valid" rounded color="primary">Cp nht thng tin c nhn</v-btn>
                                            </v-col>
                                        </v-row>
                                        <v-row v-if="userInfo.role == 'PRACTITIONER'">
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8" v-if="userInfo.role != 'COORDINATOR' && userInfo.role != 'PATIENT'">
                                                <v-autocomplete multiple label="Chuyn mn" :items="allSpecialties" item-text="name" item-value="id" v-model="userInfo.specialties"></v-autocomplete>
                                            </v-col>
                                        </v-row>
                                        <v-row v-if="userInfo.role == 'PRACTITIONER'">
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                                                <v-btn @click="updateSpecialties(userInfo.specialties, true)" rounded color="primary">Cp nht chuyn mn</v-btn>
                                            </v-col>
                                        </v-row>
                                        <v-row v-if="userInfo.role == 'SPECIALIST' || userInfo.role == 'DIETITIAN'">
                                            <v-col xs="12" sm="12" md="8" lg="8" xl="8" v-if="userInfo.role != 'COORDINATOR' && userInfo.role != 'PATIENT'">
                                                <v-autocomplete v-if="userInfo.specialty != null" label="Chuyn mn" :items="allSpecialties" item-text="name" item-value="id" v-model="userInfo.specialty.id"></v-autocomplete>
                                                <v-btn v-if="userInfo.specialty != null" @click="updateSpecialties(userInfo.specialty.id, false)" rounded color="primary">Cp nht chuyn mn</v-btn>
                                                <v-autocomplete v-if="userInfo.specialty == null" label="Chuyn mn" :items="allSpecialties" item-text="name" item-value="id" v-model="specialtyId"></v-autocomplete>
                                                <v-btn v-if="userInfo.specialty == null" @click="updateSpecialties(specialtyId, false)" rounded color="primary">Cp nht chuyn mn</v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
const axios = require('axios');
import apiService from '../services/api.service'
import config from '../config'
export default {
    data(){
        return{
            valid: false,
            userInfo: null,
            file: null,
            emailRules: [
                v => !!v || 'Cha nhp E-mail',
                v => /.+@.+/.test(v) || 'E-mail khng ng nh dng',
            ],
            noEmptyRules: [
                v => !!v || 'Khng c  trng',
            ],
            phoneRules: [
                v => /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\\./0-9]*$/g.test(v) || 'Khng c  trng v ch c nhp s',
            ],
            allSpecialties: [],
            //check when specialist/dietitian specialty is null
            specialtyId: 0,
        }
    },
    methods: {
        checkString(str){
            return (str != null & str != undefined) ? str : '_'
        },
        getInfo(){
            this.$store.dispatch('turnOnLoadingDialog', 'ang ly thng tin cho ngi dng...')
            let url = `${config.apiUrl}/user`
            apiService.getApi(url).then(result=> {
                this.userInfo = result.data
                this.userInfo.gender = result.data.gender.toString()
            }).catch(error => {
                alert(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        returnRole(role){
            let result = '';
            switch(role){
                case 'PATIENT':
                    result = 'Bnh nhn';
                    break;
                case 'PRACTITIONER':
                    result = 'Bc s a khoa';
                    break;
                case 'SPECIALIST':
                    result = 'Bc s chuyn khoa';
                    break;
                case 'DIETITIAN':
                    result = 'Bc s dinh dng';
                    break;
                case 'COORDINATOR':
                    result = 'IU PHI VIN';
                    break;
                default:
                    result = '';
                    break;
            }
            return result
        },
        updateInfo(email, phone, description){
            this.$store.dispatch('turnOnLoadingDialog', 'Cp nht thng tin c nhn...')
            let body = {
                email: email,
                phone: phone
            }
            if(description){
                body.description = description
            }
            let url = `${config.apiUrl}/user`
            apiService.putApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Cp nht thng tin thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Cp nht thng tin thnh cng'})
                    // this.getInfo()
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        uploadAvatar(){
            this.$store.dispatch('turnOnLoadingDialog', 'Cp nht nh i din...')
            let formData = new FormData();
            // formData.append('file', this.file);
            formData.append('file', this.file);
            let url = `${config.apiUrl}/images/avatar`
            axios.post(url, formData, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        // 'Content-Type': 'application/x-www-form-urlencoded',
                        'Authorization': localStorage.getItem('ac_uid')
                    }
                }
            ).then(result => {
                // console.log(result)
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Cp nht nh i din thnh cng',
                        type: 'success',
                        // all other options may go here
                        })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Cp nht nh i din thnh cng'})
                    this.userInfo.avatar = result.data
                    this.file = null;
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        getAllSpecialties(){
            let url = `${config.apiUrl}/specialties`
            apiService.getApi(url).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.allSpecialties = result.data
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            })
        },
        updateSpecialties(spec, isPrac){
            // console.log(spec)
            this.$store.dispatch('turnOnLoadingDialog', 'Cp nht chuyn mn...')
            let url = `${config.apiUrl}/user/specialties`
            let body = {
                specialties: []
            }
            //Mang cac ID
            if(isPrac){
                body.specialties = spec;
            }
            //Chi nhan 1 id
            else {
                body.specialties.push(spec)
            }
            apiService.putApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Cp nht chuyn mn thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Cp nht chuyn mn thnh cng'})
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        }
    },
    created(){
        this.getInfo()
        this.getAllSpecialties()
    }
}
</script>
<style scoped>

