<template>
    <v-container>
        <v-row>
            <v-col>
                <h1>Chc nng dnh cho iu phi vin</h1>
            </v-col>
        </v-row>
        <v-row wrap row class="fill-height">
            <v-col cols="12" md="12" sm="12" lg="12" xl="12">
                <v-card height="100%" class="elevation-4">
                    <v-data-table :headers="inquiryHeadersMain" 
                        no-data-text="Khng c yu cu t vn no mi" loading-text="ang ly d liu" :loading="loadingInquiries"
                        :items="inquiries" class="elevation-0" hide-default-footer>
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title><h3>Cc yu cu t vn mi</h3></v-toolbar-title>
                                <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                                ></v-divider>
                                <v-spacer></v-spacer>
                            </v-toolbar>
                        </template>
                        <template v-slot:item.patient="{ item }">
                            {{ item.patient.name }}
                        </template>
                        <template v-slot:item.type="{ item }">
                            {{ returnInquiryType(item.type) }}
                        </template>
                        <template v-slot:item.more="{item}">
                            <a @click.stop="openAssignToPracMainDialog(item)">Gn</a>
                        </template>
                        
                        <template v-slot:footer>
                            <br>
                            <div class="text-center">
                                <v-pagination
                                    :total-visible="7"
                                    v-model="inquiryPage"
                                    :length="inquiryPages"
                                    @input="getInquiries(inquiryPage, inquiryPageSize)"
                                ></v-pagination>
                            </div>
                            <br>
                        </template>
                    </v-data-table>
                </v-card>
                <v-dialog scrollable v-model="assignToPracDialogMain" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">Gn quyn qun l bnh nhn cho Bc s a khoa</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedInquiryMain"
                                            :headers="inquiryHeaders"
                                            :items="selectInquiryMain"
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <!-- <template v-slot:expanded-item="{ item }">
                                                <td :colspan="inquiryHeaders.length"><b>Yu cu t vn: {{ item.content }}</b> </td>
                                            </template> -->
                                        </v-data-table>
                                    </v-col>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedDoctor"
                                            :headers="doctorHeaders"
                                            :items="allPractitioner"
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn Bc s ph trch</v-toolbar-title>
                                                    <v-divider
                                                        class="mx-4"
                                                        inset
                                                        vertical
                                                    ></v-divider>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue" text :disabled="selectedInquiryMain.length == 0 || selectedDoctor.length == 0" @click="assignToPrac(selectedInquiryMain[0].patient.id, selectedInquiryMain, selectedDoctor, true), assignToPracDialogMain = false">GN</v-btn>
                            <v-btn color="red" text @click="assignToPracDialogMain = false, selectedDoctor = [], selectedInquiryMain = [], selectInquiryMain = []">NG</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="12" md="12" sm="12" lg="12" xl="12">
                <v-card height="100%" class="elevation-4">
                    <v-data-table v-show="showPatient" :headers="patientHeaders" 
                        no-data-text="Khng c kt qu ph hp" loading-text="ang ly d liu" :loading="loadingPatient"
                        :items="allPatients" class="elevation-0" hide-default-footer>
                        <template v-slot:top>
                            <v-toolbar flat>
                                <v-toolbar-title><h3>Bnh nhn</h3></v-toolbar-title>
                                <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                                ></v-divider>
                                <v-spacer></v-spacer>
                                <v-menu
                                    v-model="patientSearchMenu"
                                    :close-on-content-click="false"
                                    :nudge-width="200"
                                    offset-x
                                    >
                                    <template v-slot:activator="{ on }">
                                        <v-btn
                                        color="primary"
                                        dark
                                        v-on="on"
                                        text
                                        >
                                        <v-icon>search</v-icon>
                                        Tm kim
                                        </v-btn>
                                    </template>
                                    <v-card>
                                        <v-card-title>
                                            <h4>Tm kim bnh nhn</h4>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-text-field clearable v-model="patientSearch.name" label="Tn"></v-text-field>
                                            <!-- <v-select v-model="patientSearch.status" :items="patientStatus" label="Trng thi"></v-select> -->
                                            <v-select clearable v-model="patientSearch.gender" :items="genderArr" label="Gii tnh"></v-select>
                                            <v-text-field clearable type="number" v-model="patientSearch.age" label="Tui"></v-text-field>
                                            <!-- <v-text-field v-model="patientSearch.status" label="Trng thi"></v-text-field> -->
                                            <span>
                                                <v-checkbox @change="setPatientStatusBit(0, patientStatusBitAllValue[0])" v-model="patientStatusBitAllValue[0]" label="Bnh nhn cha c bc s qun l"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(1, patientStatusBitAllValue[1])" v-model="patientStatusBitAllValue[1]" label="Bnh nhn c bc s a khoa"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(2, patientStatusBitAllValue[2])" v-model="patientStatusBitAllValue[2]" label="Bnh nhn c bc s chuyn khoa"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(3, patientStatusBitAllValue[3])" v-model="patientStatusBitAllValue[3]" label="Bnh nhn c bc s dinh dng"></v-checkbox>
                                                <!-- <v-checkbox @change="setPatientStatusBit(4, patientStatusBitAllValue[4])" v-model="patientStatusBitAllValue[4]" label="Bnh nhn c lch gi ch s sc khe"></v-checkbox> -->
                                                <v-checkbox @change="setPatientStatusBit(10, patientStatusBitAllValue[10])" v-model="patientStatusBitAllValue[10]" label="Bnh nhn hy quyn kch hot"></v-checkbox>
                                                <v-checkbox @change="setPatientStatusBit(11, patientStatusBitAllValue[11])" v-model="patientStatusBitAllValue[11]" label="Tt c bnh nhn"></v-checkbox>
                                            </span>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary" text @click="patientSearchMenu = false, patientPage = 1, getAllPatients(patientPage, patientPageSize, patientSearch)">Tm kim</v-btn>
                                            <v-btn text color="red" @click="patientSearchMenu = false">ng</v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-menu>
                            </v-toolbar>
                        </template>
                        <template v-slot:item.more="{item}">
                            <v-menu offset-y>
                                <template v-slot:activator="{ on }">
                                    <v-icon v-on="on">more_vert</v-icon>
                                </template>
                                <v-list>
                                    <v-list-item v-if="item.status != 1024" @click="getPatientDetail(item.id, true)">
                                        <v-list-item-title>Chnh sa thng tin</v-list-item-title>
                                    </v-list-item>
                                    <v-list-item v-if="item.status != 1024" @click="getPatientDetail(item.id, false)"><v-list-item-title>Gn bnh nhn</v-list-item-title></v-list-item>
                                    <v-list-item v-if="item.status != 1024" @click="deleteUserId = item.id, deleteUserTypeIsStaff = false, deleteUserDialog = true"><v-list-item-title>Hy kch hot ti khon</v-list-item-title></v-list-item>
                                    <v-list-item v-if="item.status == 1024" @click="reactivateUser(item.id, false)"><v-list-item-title>Kch hot li ti khon</v-list-item-title></v-list-item>
                                </v-list>
                            </v-menu>
                        </template>
                        <template v-slot:item.status="{item}">
                            <span v-if="item.status != 1024">ang hot ng</span>
                            <span v-else style="color: red"> ngng hot ng</span>
                        </template>
                        <template v-slot:footer>
                            <br>
                            <div class="text-center">
                                <v-pagination
                                    :total-visible="7"
                                    v-model="patientPage"
                                    :length="patientPages"
                                    @input="getAllPatients(patientPage, patientPageSize, patientSearch)"
                                ></v-pagination>
                            </div>
                            <br>
                        </template>
                    </v-data-table>
                </v-card>
                <v-dialog scrollable v-model="patientDetailDialog" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">Thng tin chi tit bnh nhn</span>
                        </v-card-title>
                        <v-card-text>
                            <v-form v-model="patientDetailValid">
                                <v-row>
                                    <v-col cols="12" sm="12" md="6">
                                        <v-text-field :rules="noEmptyRules" label="Tn" v-model="patientDetail.name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="6">
                                        <v-menu
                                            ref="patientDobMenu"
                                            v-model="patientDobMenu"
                                            :close-on-content-click="false"
                                            :return-value.sync="patientDetail.dateOfBirth"
                                            transition="scale-transition"
                                            offset-y
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field readonly label="Ngy sinh" v-model="patientDetail.dateOfBirth" v-on="on"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="patientDetail.dateOfBirth" @input="$refs.patientDobMenu.save(patientDetail.dateOfBirth), patientDobMenu = false" scrollable></v-date-picker>
                                        </v-menu>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="6">
                                        <v-select
                                            v-model="patientDetail.gender"
                                            :items="genderArr"
                                            label="Gii tnh"
                                        ></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                                        <v-text-field :rules="emailRules" label="Email" v-model="patientDetail.email"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="6" lg="6" xl="6">
                                        <v-text-field :rules="phoneRules" label="S in thoi" type="number" v-model="patientDetail.phoneNumber"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="6">
                                        <v-text-field label="a ch" v-model="patientDetail.address"></v-text-field>
                                    </v-col>
                                </v-row>
                                <!-- <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            :headers="inquiryHeaders"
                                            :items="patientDetail.inquiries"
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Cc yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row> -->
                            </v-form>
                                
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn :disabled="!patientDetailValid" color="primary" text @click="patientDetailDialog = false, updatePatientInfo(patientDetail)">Chnh sa</v-btn>
                            <v-btn color="red" text @click="patientDetailDialog = false">ng</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog scrollable v-model="assignToPracDialog" max-width="700px" persistent>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">Gn quyn qun l bnh nhn cho Bc s a khoa</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedInquiry"
                                            :headers="inquiryHeaders"
                                            :items="patientDetail.inquiries"
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn yu cu t vn</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                            <template v-slot:item.patient="{ item }">
                                                {{ item.patient.name }}
                                            </template>
                                            <template v-slot:item.type="{ item }">
                                                {{ returnInquiryType(item.type) }}
                                            </template>
                                            <!-- <template v-slot:expanded-item="{ item }">
                                                <td :colspan="inquiryHeaders.length"><b>Yu cu t vn: {{ item.content }}</b> </td>
                                            </template> -->
                                        </v-data-table>
                                    </v-col>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-data-table
                                            hide-default-footer
                                            v-model="selectedDoctor"
                                            :headers="doctorHeaders"
                                            :items="allPractitioner"
                                            single-select
                                            show-select
                                            class="elevation-4"
                                            >
                                            <template v-slot:top>
                                                <v-toolbar flat>
                                                    <v-toolbar-title>Chn Bc s a khoa</v-toolbar-title>
                                                    <v-spacer></v-spacer>
                                                </v-toolbar>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue" text :disabled="selectedInquiry.length == 0 || selectedDoctor.length == 0" @click="assignToPrac(patientDetail.id, selectedInquiry, selectedDoctor, false), assignToPracDialog = false">GN</v-btn>
                            <v-btn color="red" text @click="assignToPracDialog = false, selectedDoctor = [], selectedInquiry = []">NG</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
        <v-row>
            <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                <v-dialog scrollable v-model="registerObj.dialog" max-width="700px" persistent>
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark v-on="on">To ti khon bnh nhn</v-btn>
                    </template>
                    <v-card>
                        <v-card-title
                            class="headline primary"
                            primary-title
                            >
                            <span style="color: white">To ti khon</span>
                        </v-card-title>
                        <v-card-text>
                            <v-form v-model="registerObj.valid">
                                <v-row wrap>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-text-field prepend-icon="people" :rules="noEmptyRules" v-model="registerObj.name" label="Tn ngi dng"></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-text-field prepend-icon="email" v-model="registerObj.email" :rules="emailRules" label="Email"></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-text-field prepend-icon="call" v-model="registerObj.phoneNumber" :rules="phoneRules" label="S in thoi"></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-menu
                                            ref="menu"
                                            v-model="registerObj.dateMenu"
                                            :close-on-content-click="false"
                                            :return-value.sync="registerObj.dateOfBirth"
                                            transition="scale-transition"
                                            offset-y
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                    readonly
                                                    v-model="registerObj.dateOfBirth"
                                                    label="Ngy sinh"
                                                    prepend-icon="event"
                                                    v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-date-picker v-model="registerObj.dateOfBirth" no-title scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="$refs.menu.save(registerObj.dateOfBirth)">CHN</v-btn>
                                            <v-btn text color="red" @click="registerObj.dateMenu = false">NG</v-btn>
                                            
                                            </v-date-picker>
                                        </v-menu>
                                    </v-col>
                                </v-row>
                                <v-row row wrap>
                                    <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                        Gii tnh
                                        <v-radio-group v-model="registerObj.gender" row>
                                            <v-radio label="Nam" value="0"></v-radio>
                                            <v-radio label="N" value="1"></v-radio>
                                            <v-radio label="Khc" value="2"></v-radio>
                                        </v-radio-group>
                                    </v-col>
                                    <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                        Chc v
                                        <v-select readonly v-model="registerObj.role" :items="registerObj.roles"></v-select>
                                    </v-col>
                                </v-row>
                                <v-row >
                                    <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                                        <v-text-field
                                            prepend-icon="lock"
                                            v-model="registerObj.password"
                                            label="Mt khu"
                                            :rules="noEmptyRules"
                                            type="password"
                                        ></v-text-field>
                                    </v-col>
                                </v-row>
                                
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" :disabled="!registerObj.valid" text @click="registerObj.dialog = false, register(registerObj.name, registerObj.email, registerObj.phoneNumber, registerObj.dateOfBirth, registerObj.gender, registerObj.role, registerObj.password)">To</v-btn>
                            <v-btn color="red" text @click="registerObj.dialog = false">NG</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
        <!-- dialog xa ti khon -->
        <v-dialog
            persistent
            v-model="deleteUserDialog"
            width="400"
            >
            <v-card>
                <v-card-title
                    class="headline red"
                    primary-title
                    dark
                    >
                    <span style="color: white">Xc nhn hy kch hot ti khon</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <br>
                    Bn c chc chn mun hy kch hot ti khon ny? 
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="red"
                    text
                    @click="deleteUserDialog = false, deleteUser(deleteUserId, deleteUserTypeIsStaff)"
                    >
                    XC NHN
                </v-btn>
                <v-btn
                    color="primary"
                    text
                    @click="deleteUserDialog = false, deleteUserId = 0, deleteUserTypeIsStaff = null"
                    >
                    NG
                </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>
<script>
// const { encode } = require('url-encode-decode')
import {mapGetters} from 'vuex'
// import eventBus from '../../eventBus'
import apiService from '../../services/api.service'
import config from '../../config'
var convertNumber = require('decimal-to-binary')
export default {
    data(){
        return {
            doctorHeaders: [
                { text: 'ID', value: 'id' , align: 'start'},
                { text: 'TN', value: 'name', align: 'start' },
                { text: 'EMAIL', value: 'email', align: 'start' },
                { text: 'S T', value: 'phoneNumber', align: 'start' },
            ],
            loadingDoctor: false,
            deleteUserDialog: false,
            deleteUserId: 0,
            deleteUserTypeIsStaff: null,
            allPatients: [],
            patientHeaders: [
                { text: 'ID', value: 'id' , align: 'start'},
                { text: 'TN', value: 'name', align: 'start' },
                { text: 'TUI', value: 'age', align: 'start' },
                { text: 'TRNG THI', value: 'status', align: 'start' },
                { text: 'CHN HNH NG', value: 'more', align: 'end' },
            ],
            patientPage: 1,
            patientPageSize: 10,
            patientPages: 1,
            showPatient: true,
            patientDetailDialog: false,
            loadingPatient: false,
            patientDetail: {
                dateOfBirth: '',
                createdDate: '',
                email: '',
                gender: '',
                id: '',
                inquiries: [],
                name: '',
                phoneNumber: '',
                practitioner: '',
            },
            patientDetailValid: true,
            patientDobMenu: false,
            inquiryHeaders: [
                { text: 'ID', value: 'id' , align: 'start'},
                { text: 'TN BNH NHN', value: 'patient', align: 'start' },
                { text: 'KIU YU CU', value: 'type', align: 'start' },
                // { text: '', value: 'data-table-expand' },
                {text: 'NI DUNG', value: 'content', align: 'start'}
            ],
            assignToPracDialog: false,
            patientSearch: {
                name: undefined,
                status: 1,
                gender: undefined,
                age: undefined
            },
            patientSearchMenu: false,
            patientStatus: [
                {
                    text: 'Cha c bc s qun l',
                    value: 1
                },
                {
                    text: 'Ngng hot ng',
                    value: 1024
                }
            ],
            patientStatusBitAllValue: [false, false, false, false, false, false, false, false, false, false, false, false],
            
            registerObj: {
                dateOfBirth: '',
                dateMenu: false,
                valid: false,
                dialog: false,
                email: '',
                gender: "0",
                name: '',
                password: '',
                phoneNumber: '',
                role: 4,
                roles: [
                    // {
                    //     text: 'a khoa',
                    //     value: 0
                    // },
                    // {
                    //     text: 'Chuyn khoa',
                    //     value: 1
                    // },
                    // {
                    //     text: 'Dinh dng',
                    //     value: 2
                    // },
                    // {
                    //     text: 'iu phi vin',
                    //     value: 3
                    // },
                    {
                        text: 'Bnh nhn',
                        value: 4
                    }
                ],
            },
            emailRules: [
                v => !!v || 'Cha nhp E-mail',
                v => /.+@.+/.test(v) || 'E-mail khng ng nh dng',
            ],
            noEmptyRules: [
                v => !!v || 'Khng c  trng',
            ],
            phoneRules: [
                v => /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\\./0-9]*$/g.test(v) || 'Khng c  trng v ch c nhp s',
            ],
            allPractitioner: [],
            selectedInquiry: [],
            selectedDoctor: [],
            genderArr: [
                {
                    text: 'Nam',
                    value: 0
                },
                {
                    text: 'N',
                    value: 1
                },
                {
                    text: 'Khc',
                    value: 2
                },
            ],
            inquiries: [],
            loadingInquiries: false,
            inquiryPage: 1,
            inquiryPageSize: 10,
            inquiryPages: 0,
            inquiryHeadersMain: [
                { text: 'ID', value: 'id' , align: 'start'},
                { text: 'TN BNH NHN', value: 'patient', align: 'start' },
                { text: 'KIU YU CU', value: 'type', align: 'start' },
                // { text: '', value: 'data-table-expand' },
                {text: 'NI DUNG', value: 'content', align: 'start'},
                {text: 'GN', value: 'more', align: 'end'}
            ],
            selectInquiryMain: [],
            selectedInquiryMain: [],
            assignToPracDialogMain: false,
            inquiryAssignValue: [
                {
                    text: 'Tt c cc yu cu',
                    value: true
                },
                {
                    text: 'Yu cu cha c gn cho bc s cp di',
                    value: false
                },
            ],
            inquiryAssign: true,
        }
    },
    computed: {
        ...mapGetters({
            newNotification: 'newNotification',
            handleNotificationObj: 'handleNotificationObj'
        })
    },
    watch: {
        newNotification(){
            this.handleNewNotification(this.newNotification)
        }
    },
    methods: {
        setPatientStatusBit(index, value){
            // console.log(index)
            // console.log(value)
            if(value == true){
                switch(index){
                    //new patient
                    case 0: {
                        this.patientStatusBitAllValue[1] = 0;
                        this.patientStatusBitAllValue[2] = 0;
                        this.patientStatusBitAllValue[3] = 0;
                        this.patientStatusBitAllValue[10] = 0;
                        this.patientStatusBitAllValue[11] = 0;
                        break;
                    }
                    //not new patient
                    case 1:
                    case 2:
                    case 3: {
                        this.patientStatusBitAllValue[0] = 0;
                        this.patientStatusBitAllValue[10] = 0;
                        this.patientStatusBitAllValue[11] = 0;
                        break;
                    }
                    // //schedule
                    // case 4: {
                    //     this.patientStatusBitAllValue[10] = 0;
                    //     this.patientStatusBitAllValue[11] = 0;
                    //     break;
                    // }
                    //deactivated
                    case 10: {
                        this.patientStatusBitAllValue[0] = 0;
                        this.patientStatusBitAllValue[1] = 0;
                        this.patientStatusBitAllValue[2] = 0;
                        this.patientStatusBitAllValue[3] = 0;
                        this.patientStatusBitAllValue[4] = 0;
                        this.patientStatusBitAllValue[11] = 0;
                        break;
                    }
                    //all patient
                    case 11: {
                        this.patientStatusBitAllValue[0] = 0;
                        this.patientStatusBitAllValue[1] = 0;
                        this.patientStatusBitAllValue[2] = 0;
                        this.patientStatusBitAllValue[3] = 0;
                        this.patientStatusBitAllValue[4] = 0;
                        this.patientStatusBitAllValue[10] = 0;
                        break;
                    }
                    default: {
                        break;
                    }
                }
            }
            // console.log(this.returnValueFromStatusBit())
        },
        returnValueFromStatusBit(){
            if(this.patientStatusBitAllValue[11] == true){
                return -1
            }
            else if (this.patientStatusBitAllValue[10] == true){
                return 1024
            }
            else if (this.patientStatusBitAllValue[0]  == true){
                return (this.patientStatusBitAllValue[4] == true) ? 17 : 1
            }
            else {
                let bitArr = []
                for(let i = 0; i < this.patientStatusBitAllValue.length - 1 ; i++){
                    bitArr[i] = (this.patientStatusBitAllValue[i] == true) ? 1 : 0
                }
                let statusValue = bitArr.reverse().join("")
                // console.log(statusValue)
                //has been reverse
                // console.log(bitArr)
                if(bitArr[9] == 0 && (bitArr[8] == 1 || bitArr[7] == 1)){
                    return convertNumber.convertToDecimal(statusValue) + 1;
                }
                else {
                    return convertNumber.convertToDecimal(statusValue);
                }
            }
        },
        returnInquiryType(type){
            return (type == 0) ? 'Khm bnh' : 'Dinh dng'
        },
        returnGender(number){
            switch(number){
                case 0: 
                    return 'Nam'
                case 1:
                    return 'N'
                case 2:
                    return 'Khc'
            }
        },
        checkString(str){
            return (str != null & str != undefined) ? str : '_'
        },
        returnRole(role){
            let result = '';
            switch(role){
                case 'PATIENT':
                    result = 'Bnh nhn';
                    break;
                case 'PRACTITIONER':
                    result = 'Bc s a khoa';
                    break;
                case 'SPECIALIST':
                    result = 'Bc s chuyn khoa';
                    break;
                case 'DIETITIAN':
                    result = 'Bc s dinh dng';
                    break;
                case 'COORDINATOR':
                    result = 'IU PHI VIN';
                    break;
                default:
                    result = '';
                    break;
            }
            return result
        },
        getAllPatients(page, size, searchObj){
            this.loadingPatient = true;
            this.allPatients = [];
            let params = {
                page: page,
                size: size,
            }
            searchObj.status = (this.returnValueFromStatusBit() == 0) ? 1 : this.returnValueFromStatusBit()
            let searchString = '';
            //Loop through all property of patientSearch Obj
            for (const property in searchObj) {
                if(searchObj[property] != undefined && searchObj[property] != null && searchObj[property] != ''){
                    searchString += `${property}=${searchObj[property]},`
                }
                if(searchObj[property] == 0){
                    searchString += `${property}=${searchObj[property]},`
                }
            }
            if(searchString != ''){
                params.search = searchString
            }
            let url = `${config.apiUrl}/patients`
            apiService.getApi(url, params).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.patientPages = result.data.totalPages
                    this.updatePatients(result.data.content)
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
                
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.loadingPatient = false;
            })
        },
        updatePatients(patArray){
            this.allPatients = patArray;
        },
        goToPatientPage(id){
            let url = `/patient/${id}`
            this.$router.push(url)
        },
        getPatientDetail(id, isDetail){
            this.$store.dispatch('turnOnLoadingDialog', 'ang ly thng tin chi tit bnh nhn...')
            let url = `${config.apiUrl}/patients/${id}`
            let params = {
                id: id
            }
            apiService.getApi(url, params).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.processPatientDetailFromServer(result.data, isDetail)
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        processPatientDetailFromServer(data, isDetail){
            this.patientDetail = data;
            this.patientDetail.dateOfBirth = data.dateOfBirth.split("/").reverse().join("-")
            // this.patientDetail.createdDate = data.createdDate
            // this.patientDetail.email = data.email
            // this.patientDetail.gender = data.gender
            // this.patientDetail.id = data.id
            // this.patientDetail.inquiries = data.inquiries
            // this.patientDetail.name = data.name
            // this.patientDetail.phoneNumber = data.phoneNumber
            // this.patientDetail.address = data.address
            if(isDetail == true){
                this.patientDetailDialog = true
            }
            else {
                this.assignToPracDialog = true
            }
        },
        assignToPrac(patientId, inquiry, doctor, fromMainTable){
            this.$store.dispatch('turnOnLoadingDialog', 'ang gn bnh nhn cho bc s...')
            let url = `${config.apiUrl}/patients/${patientId}/doctor`
            let body = {
                "doctor_id": doctor[0].id,
                "inquiry_id": inquiry[0].id
            }
            // console.log(url)
            // console.log(body)
            // console.log(fromMainTable)
            apiService.postApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Gn quyn qun l bnh nhn thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Gn quyn qun l bnh nhn thnh cng!'})
                    if(fromMainTable){
                        this.selectedDoctor = []
                        this.selectedInquiryMain = []
                        this.selectInquiryMain = []
                        
                    }
                    else {
                        this.selectedDoctor = []
                        this.selectedInquiry = []
                        
                        
                    }
                    this.inquiryPage = 1;
                    this.getInquiries(this.inquiryPage, this.inquiryPageSize)
                    this.patientPage = 1;
                    this.patientSearch = {
                        name: undefined,
                        status: 1,
                        gender: undefined,
                        age: undefined
                    }
                    this.getAllPatients(this.patientPage, this.patientPageSize, this.patientSearch)
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        register(name, email, phone, dob, gender, role, password){
            this.$store.dispatch('turnOnLoadingDialog', 'ang To ti khon mi...')
            let body = {
                name: name,
                email: email,
                phone: phone,
                dob: dob.split("-").reverse().join("/"),
                gender: parseInt(gender, 10),
                role: role,
                password: password
            }
            let url = `${config.apiUrl}/auth/sign_up`
            apiService.postApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'To ti khon thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'To ti khon thnh cng'})
                    this.patientPage = 1;
                    this.patientSearch = {
                        name: undefined,
                        status: 1,
                        gender: undefined,
                        age: undefined
                    }
                    this.getAllPatients(this.patientPage, this.patientPageSize, this.patientSearch)
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        deleteUser(id, isStaff){
            this.$store.dispatch('turnOnLoadingDialog', 'ang hy kch hot ti khon ny...')
            let url = `${config.apiUrl}/user`
            let body = {
                id: id
            }
            apiService.deleteApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Hy kch hot ti khon thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Xa ti khon thnh cng'})
                    if(isStaff){
                        this.doctorPage = 1;
                        this.getAllDoctor(this.doctorPage, this.doctorPageSize, this.doctorSearch)
                    }
                    else {
                        this.patientPage = 1;
                        this.getAllPatients(this.patientPage, this.patientPageSize, this.patientSearch)
                    }
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
                
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
                this.deleteUserId = 0;
                this.deleteUserTypeIsStaff = null;
            })
        },
        reactivateUser(id, isStaff){
            this.$store.dispatch('turnOnLoadingDialog', 'ang ti kch hot ti khon ny...')
            let url = `${config.apiUrl}/user`
            let body = {
                id: id
            }
            apiService.postApi(url, body).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.$toast.open({
                        message: 'Ti kch hot ti khon thnh cng',
                        type: 'success',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'success', message: 'Ti kch hot ti khon thnh cng'})
                    if(isStaff){
                        this.doctorPage = 1;
                        this.getAllDoctor(this.doctorPage, this.doctorPageSize, this.doctorSearch)
                    }
                    else {
                        this.patientPage = 1;
                        this.getAllPatients(this.patientPage, this.patientPageSize, this.patientSearch)
                    }
                }
                else {
                    this.$toast.open({
                        message: result.data.message,
                        type: 'error',
                        // all other options may go here
                    })
                    // this.$store.dispatch('turnOnAlert', {color: 'error', message: result.data.message})
                }
                
            }).catch(error => {
                console.log(error)
            }).finally(() => {
                this.$store.dispatch('turnOffLoadingDialog')
            })
        },
        getAllPractitioner(){
            let params = {
                page: 1,
                size: 1000,
                search: 'role=0,status=1'
            }
            let url = `${config.apiUrl}/doctors`
            apiService.getApi(url, params).then(result => {
                if(result.status.toString()[0] === "2"){
                    this.allPractitioner = result.data.content
